<template>
  <div id="avatar">
    <span :title="user.username">{{slug}}</span>
  </div>
</template>

<script>
export default {
  name: "Avatar",
  data(){
    return{
      user: {
        username: 'person1'
      },
      slug: 'P'
    }
  }
}
</script>

<style scoped>
#avatar{

}
span{
  display: inline-block;
  width: 30px;
  height: 30px;
  text-align: center;
  line-height: 32px;
  border-radius: 100%;
  background: #f2b81c;
  color: #fff;
  text-shadow: 1px 0 1px #795c19;
  font-weight: bold;
  font-size: 18px;
  text-transform: uppercase;
  margin-top: 15px;
}
</style>



